<template>
        <div 
            class="c100 tiny orange dark"
            v-bind:class="timerPercentage"
            
        >
            <span class="c_white">{{timerString}}</span>
            <div class="slice">
                <div class="bar"></div>
                <div class="fill"></div>
            </div>
        </div>
</template>

<script>
export default {
	props: {
		startTime: { type: Number, default: 5 },
        criticalTime: {type: Number, default: 1}
	},
    data() {
        return {
            intervalId: "",
            timerString: "0"+this.startTime+":00",
            criticalTimerString: "0"+this.criticalTime+":00"
        }
    },
    computed: {
        timerPercentage() {
            let totalSeconds = 60 * parseInt(this.startTime);
            let secondsRemaining = 60 * parseInt(this.timerString.split(":")[0]) + parseInt(this.timerString.split(":")[1]);
            let criticalSeconds = 60 * parseInt(this.criticalTime);

            let percent = parseInt(secondsRemaining / totalSeconds * 100);
            let cls = {};
            cls['p'+percent] = true;
            cls['timer-critical'] = secondsRemaining < criticalSeconds;
            return cls;
        },
        isTimerCritical() {
            let minutes = parseInt(this.timerString.split(":")[0]);
            let seconds = parseInt(this.timerString.split(":")[1]);
            let criticalMinutes = parseInt(this.criticalTimerString.split(":")[0]);
            let criticalSeconds = parseInt(this.criticalTimerString.split(":")[1]);

            if(minutes < criticalMinutes) {
                return true;
            } else if(minutes === criticalMinutes) {
                if(seconds < criticalSeconds) {
                    return true;
                } else {
                    return false;
                }
            } else {
                return false;
            }
        }
    },
    methods: {
        getTime() {
            return this.timerString;
        },
        resetTimer: function() {
			this.timerString = this.startTime;
			if(this.intervalId)
				clearInterval(this.intervalId);
			this.intervalId = "";
            this.$emit("timer-reset");
		},
		updateTimer: function() {
			var state = this;
			var minutes = parseInt(state.timerString.split(":")[0]);
			var seconds = minutes * 60 + parseInt(state.timerString.split(":")[1]);
			seconds--;
			minutes = parseInt(seconds / 60);
			seconds = seconds % 60;
			if(seconds < 10)
				seconds = "0"+seconds;
			if(minutes < 10)
				minutes = "0"+minutes;

			state.timerString = minutes + ":" + seconds;
		},
		startTimer: function() {
			var self = this;
			if(this.intervalId)
				return;
			this.intervalId = setInterval(function() {
				self.updateTimer();
				if(self.timerString === "00:00") {
                    if(self.intervalId)
                        clearInterval(self.intervalId);
                    self.intervalId = "";	
					self.$emit("time-expired");
				}
			}, 1000);
		},
		stopTimer: function() {
			if(this.intervalId)
				clearInterval(this.intervalId);
			this.intervalId = "";	
            this.$emit("timer-stopped");
		},
    }
};
</script>

